 \r\n      <div class=\"user-section\">\r\n        <template v-if=\"authStore.isAuthenticated\">\r\n          <router-link \r\n            v-if=\"authStore.isAdmin\" \r\n            to=\"/admin/dashboard\" \r\n            class=\"nav-link admin-link\">\r\n            <el-icon><Tools /></el-icon>\r\n            管理后台\r\n          </router-link>\r\n          <el-dropdown @command=\"handleUserCommand\">\r\n            <div class=\"user-avatar\">\r\n              <img :src=\"authStore.user.avatarUrl\" :alt=\"authStore.user.username\" />\r\n              <span>{{ authStore.user.username }}</span>\r\n            </div>\r\n            <template #dropdown>\r\n              <el-dropdown-menu>\r\n                <el-dropdown-item :command=\"`profile:${authStore.user.username}`\">\r\n                  <el-icon><User /></el-icon>\r\n                  我的主页\r\n                </el-dropdown-item>\r\n                <el-dropdown-item command=\"logout\" divided>\r\n                  <el-icon><SwitchButton /></el-icon>\r\n                  退出登录\r\n                </el-dropdown-item>\r\n              </el-dropdown-menu>\r\n            </template>\r\n          </el-dropdown>\r\n        </template>\r\n        <template v-else>\r\n          <router-link to=\"/login\" class=\"btn-primary\">登录</router-link>\r\n          <router-link to=\"/register\" class=\"btn-secondary\">注册</router-link>\r\n        </template>\r\n      </div>\r\n    </div>\r\n  </header>\r\n</template>\r\n\r\n<script setup>\r\n// Global header\r\n// - Mode badge toggles between VULN and SECURE (requires re-login)\r\nimport { computed } from 'vue';\r\nimport { useRouter } from 'vue-router';\r\nimport { useAuthStore } from '@/stores/auth';\r\nimport { useConfigStore } from '@/stores/config';\r\nimport { ElMessage, ElMessageBox } from 'element-plus';\r\n\r\nconst router = useRouter();\r\nconst authStore = useAuthStore();\r\nconst configStore = useConfigStore();\r\n\r\nconst modeText = computed(() => configStore.xssMode === 'vuln' ? 'VULN' : 'SECURE');\r\nconst modeBadgeClass = computed(() => configStore.xssMode === 'vuln' ? 'badge-vuln' : 'badge-secure');\r\n\r\nconst toggleMode = async () => {\r\n  const newMode = configStore.xssMode === 'vuln' ? 'secure' : 'vuln';\r\n  const modeText = newMode === 'vuln' ? 'VULN（漏洞）' : 'SECURE（安全）';\r\n  \r\n  try {\r\n    await ElMessageBox.confirm(\r\n      `确认切换到 ${modeText} 模式？`,\r\n      '切换模式',\r\n      {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        type: 'info'\r\n      }\r\n    );\r\n    \r\n    await configStore.switchMode(newMode);\r\n    window.__XSS_MODE__ = newMode;\r\n    \r\n    ElMessage.success(`已切换到 ${modeText} 模式`);\r\n  } catch (error) {\r\n    if (error !== 'cancel') {\r\n      ElMessage.error('切换模式失败');\r\n    }\r\n  }\r\n};\r\n\r\nconst handleUserCommand = async (command) => {\r\n  if (command === 'logout') {\r\n    await authStore.logout();\r\n    ElMessage.success('已退出登录');\r\n    router.push('/');\r\n  } else if (command.startsWith('profile:')) {\r\n    const username = command.split(':')[1];\r\n    router.push(`/profile/${username}`);\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.app-header {\r\n  background: var(--color-bg-card);\r\n  border-bottom: 1px solid var(--color-border);\r\n  padding: var(--spacing-md) 0;\r\n  position: sticky;\r\n  top: 0;\r\n  z-index: 100;\r\n  backdrop-filter: blur(10px);\r\n}\r\n\r\n.header-content {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  gap: var(--spacing-xl);\r\n}\r\n\r\n.logo-section {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.logo {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-sm);\r\n  font-size: 1.5rem;\r\n  font-weight: 700;\r\n  color: var(--color-text-primary);\r\n  text-decoration: none;\r\n}\r\n\r\n.logo-icon {\r\n  font-size: 2rem;\r\n}\r\n\r\n.logo-text {\r\n  background: linear-gradient(135deg, var(--color-primary), var(--color-accent));\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n  background-clip: text;\r\n}\r\n\r\n.badge {\r\n  cursor: pointer;\r\n  transition: transform 0.2s, opacity 0.2s;\r\n}\r\n\r\n.badge:hover {\r\n  transform: scale(1.1);\r\n  opacity: 0.8;\r\n}\r\n\r\n.nav-menu {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-lg);\r\n  flex: 1;\r\n}\r\n\r\n.nav-link {\r\n  color: var(--color-text-secondary);\r\n  font-weight: 500;\r\n  padding: 8px 16px;\r\n  border-radius: var(--radius-sm);\r\n  transition: all var(--transition-fast);\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-xs);\r\n}\r\n\r\n.nav-link:hover,\r\n.nav-link.router-link-active {\r\n  color: var(--color-primary);\r\n  background: rgba(34, 211, 238, 0.1);\r\n}\r\n\r\n.admin-link {\r\n  color: var(--color-accent);\r\n}\r\n\r\n.admin-link:hover {\r\n  background: rgba(251, 146, 60, 0.1);\r\n}\r\n\r\n.user-section {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-md);\r\n}\r\n\r\n.user-avatar {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-sm);\r\n  padding: 6px 12px;\r\n  border-radius: var(--radius-sm);\r\n  cursor: pointer;\r\n  transition: background var(--transition-fast);\r\n}\r\n\r\n.user-avatar:hover {\r\n  background: var(--color-bg-elevated);\r\n}\r\n\r\n.user-avatar img {\r\n  width: 32px;\r\n  height: 32px;\r\n  border-radius: 50%;\r\n  border: 2px solid var(--color-primary);\r\n}\r\n\r\n.user-avatar span {\r\n  font-weight: 500;\r\n  color: var(--color-text-primary);\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  .nav-menu {\r\n    display: none;\r\n  }\r\n  \r\n  .header-content {\r\n    gap: var(--spacing-md);\r\n  }\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
